// RadialBarChart.js
import ReactApexChart from 'react-apexcharts';

const RadialBarChart = ({ value, label }) => {
  const minVoltage = 23.0; // Set the minimum voltage
  const maxVoltage = 24.8; // Set the maximum voltage
  const percentage = ((value - minVoltage) / (maxVoltage - minVoltage)) * 100; // Calculate percentage based on 23V to 24.8V range

  // Ensure percentage is between 0 and 100
  const chartPercentage = Math.max(0, Math.min(percentage, 100));

  const chartOptions = {
    chart: {
      type: 'radialBar',
    },
    plotOptions: {
      radialBar: {
        startAngle: -135,
        endAngle: 135,
        hollow: {
          size: '60%',
        },
        dataLabels: {
          name: {
            show: true,
            fontSize: '22px',
          },
          value: {
            show: true,
            fontSize: '22px',
            formatter: (val) => `${Math.round(val)}%`, // Display percentage
          },
        },
      },
    },
    fill: {
      type: 'solid',
      colors: ['#04ADBF', '#607d8b'],
    },
    labels: ['Voltage'],
    stroke: {
      dashArray: 2,
    },
  };

  return (
    <div id="chart">
      <ReactApexChart
        options={chartOptions}
        series={[chartPercentage]} // Pass the calculated percentage as the value
        type="radialBar"
        height={350}
      />
    </div>
  );
};

export default RadialBarChart;
