import React, { useEffect, useState, useCallback, useMemo } from 'react';
import RadialBarChart from './Charts/Graphs';

function Home() {
  const [data, setData] = useState(null);

  // Use useMemo to create notificationSound only once
  const notificationSound = useMemo(() => new Audio('sound.mp3'), []);

  const sendNotification = useCallback((value) => {
    notificationSound.play();  // Play the sound

    if ("Notification" in window && Notification.permission === "granted") {
      new Notification("Warning: Low Data Value", {
        body: `The value is below 23.9: ${value}`,
      });
    } else if (Notification.permission !== "denied") {
      Notification.requestPermission().then(permission => {
        if (permission === "granted") {
          new Notification("Warning: Low Data Value", {
            body: `The value is below 23.9: ${value}`,
          });
        }
      });
    }
  }, [notificationSound]); // No more changing dependency

  useEffect(() => {
    const getTimeMinusOneMinute = () => {
      const now = new Date();
      now.setMinutes(now.getMinutes() - 1);
      const hours = String(now.getHours()).padStart(2, '0');
      const minutes = String(now.getMinutes()).padStart(2, '0');
      const seconds = String(now.getSeconds()).padStart(2, '0');
      return `${hours}:${minutes}:${seconds}`;
    };

    const fetchData = async () => {
      const time = getTimeMinusOneMinute();
      const url = `https://iot.tharindur.com/mppt/api/json.php?fn=getUpdate&t=${time}`;

      try {
        const response = await fetch(url);
        const result = await response.json();
        
        if (result && result[0]) {
          const lastArray = result[0][result[0].length - 1];
          const batVolts = parseFloat(lastArray[2]); // Convert to a number

          setData(lastArray);
          
          if (batVolts < 23.9) {
            sendNotification(batVolts);
          }
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
    const intervalId = setInterval(fetchData, 60000); // Fetch every minute

    return () => clearInterval(intervalId);
  }, [sendNotification]); // Add sendNotification as a dependency

  return (
    <div>
      <h1>Fetched Data</h1>
      {data !== null ? <p>Time: {data[1]}</p> : <p>Loading...</p>}
      {data !== null ? <p>Battery: {data[2]}</p> : <p>Loading...</p>}
      {data !== null ? <p>Solar: {data[3]}</p> : <p>Loading...</p>}
      <div className='row'>
        <div className='two-col'><RadialBarChart value={data[2]}/></div>
        <div className='two-col'><RadialBarChart value={data[3]}/></div>
      </div>
    </div>
  );
}

export default Home;